'use strict';

var rotatorButtons = document.getElementsByClassName('rotator');
var rotatorIdPostfix = -"_rotate".length; // reverse value to negative
var degreesPerTurn = 90;

/**
 * Rotate image handler
 */
var rotateImage = function rotateImage() {
    var photoContainerID = this.id.slice(0, rotatorIdPostfix); // from the end of the ID string
    var photoContainer = document.getElementById(photoContainerID);

    // use element attribute data-rotate to get and store degrees for rotation
    var dataRotate = +photoContainer.getAttribute('data-rotate');
    dataRotate += degreesPerTurn;
    photoContainer.dataset.rotate = dataRotate.toString();

    photoContainer.style.transform = "rotate(" + dataRotate + "deg)";

    // if element has initial replacement
    // or upside down
    if (dataRotate % 180 === 0) {
        photoContainer.classList.remove("rotated");
    } else {
        photoContainer.classList.add("rotated");
    }
};

for (var i = 0; i < rotatorButtons.length; i++) {
    rotatorButtons[i].addEventListener("click", rotateImage, false);
}